"use client"

import { useState, useEffect } from "react"
import styles from "./styles/home.module.css"
import RecommendationButton from '@/Button/RecommendationButton'

export default function Home() {
  const [beans, setBeans] = useState([])
  const [filteredBeans, setFilteredBeans] = useState([])
  const [searchTerm, setSearchTerm] = useState("")
  const [filterOrigin, setFilterOrigin] = useState("")
  const [filterRoast, setFilterRoast] = useState("")
  const [sortBy, setSortBy] = useState("latest")
  const [favorites, setFavorites] = useState([])
  const [cartCount, setCartCount] = useState(0)
  const [showFilters, setShowFilters] = useState(false)
  const [currentUser, setCurrentUser] = useState(null)

  useEffect(() => {
    const storedBeans = JSON.parse(localStorage.getItem("beans") || "[]")
    setBeans(storedBeans)
    setFilteredBeans(storedBeans)

    const storedFavorites = JSON.parse(localStorage.getItem("favorites") || "[]")
    setFavorites(storedFavorites)

    const cart = JSON.parse(localStorage.getItem("cart") || "[]")
    setCartCount(cart.length)

    const user = JSON.parse(localStorage.getItem("currentUser") || "null")
    setCurrentUser(user)
  }, [])

  useEffect(() => {
    let result = [...beans]

    // Í≤ÄÏÉâ
    if (searchTerm) {
      result = result.filter(
        (bean) =>
          bean.beanName.toLowerCase().includes(searchTerm.toLowerCase()) ||
          bean.origin.toLowerCase().includes(searchTerm.toLowerCase()),
      )
    }

    // ÏõêÏÇ∞ÏßÄ ÌïÑÌÑ∞
    if (filterOrigin) {
      result = result.filter((bean) => bean.origin === filterOrigin)
    }

    // Î°úÏä§ÌåÖ ÌïÑÌÑ∞
    if (filterRoast) {
      result = result.filter((bean) => bean.roastLevel === filterRoast)
    }

    // Ï†ïÎ†¨
    if (sortBy === "latest") {
      result.sort((a, b) => new Date(b.createdAt) - new Date(a.createdAt))
    } else if (sortBy === "price-low") {
      result.sort((a, b) => Number.parseInt(a.price) - Number.parseInt(b.price))
    } else if (sortBy === "price-high") {
      result.sort((a, b) => Number.parseInt(b.price) - Number.parseInt(a.price))
    } else if (sortBy === "popular") {
      result.sort((a, b) => (b.likes || 0) - (a.likes || 0))
    }

    setFilteredBeans(result)
  }, [searchTerm, filterOrigin, filterRoast, sortBy, beans])

  const toggleFavorite = (beanId) => {
    const newFavorites = favorites.includes(beanId) ? favorites.filter((id) => id !== beanId) : [...favorites, beanId]

    setFavorites(newFavorites)
    localStorage.setItem("favorites", JSON.stringify(newFavorites))
  }

  const addToCart = (bean) => {
    const cart = JSON.parse(localStorage.getItem("cart") || "[]")
    cart.push(bean)
    localStorage.setItem("cart", JSON.stringify(cart))
    setCartCount(cart.length)
    alert("Ïû•Î∞îÍµ¨ÎãàÏóê Ï∂îÍ∞ÄÎêòÏóàÏäµÎãàÎã§!")
  }

  const handleLogout = () => {
    localStorage.removeItem("currentUser")
    setCurrentUser(null)
    alert("Î°úÍ∑∏ÏïÑÏõÉÎêòÏóàÏäµÎãàÎã§.")
    window.location.href = "/"
  }

  const origins = [...new Set(beans.map((bean) => bean.origin))].filter(Boolean)

  return (
    <div className={styles.container}>
      <header className={styles.header}>
        <div className={styles.logo}>DabeanChi</div>
        <nav className={styles.nav}>
          <a href="/" className={styles.navLink}>
            Ìôà
          </a>
          {currentUser ? (
            <>
              <a href="/profile" className={styles.navLink}>
                ÌîÑÎ°úÌïÑ
              </a>
              <a href="/cart" className={styles.navLink}>
                Ïû•Î∞îÍµ¨Îãà ({cartCount})
              </a>
              <button onClick={handleLogout} className={styles.logoutButton}>
                Î°úÍ∑∏ÏïÑÏõÉ
              </button>
              <RecommendationButton />
            </>
          ) : (
            <>
              <a href="/cart" className={styles.navLink}>
                Ïû•Î∞îÍµ¨Îãà ({cartCount})
              </a>
              <a href="/login" className={styles.loginButton}>
                Î°úÍ∑∏Ïù∏
              </a>
            </>
          )}
        </nav>
      </header>

      <section className={styles.hero}>
        <h1 className={styles.heroTitle}>ÎãπÏã†Ïùò ÏõêÎëêÎ•º Í≥µÏú†ÌïòÏÑ∏Ïöî</h1>
        <p className={styles.heroSubtitle}>ÏßÅÏ†ë Î°úÏä§ÌåÖÌïú ÏõêÎëêÎ•º ÌåêÎß§ÌïòÍ≥†, ÌäπÎ≥ÑÌïú ÏõêÎëêÎ•º Î∞úÍ≤¨ÌïòÏÑ∏Ïöî</p>
        <a href="/register-bean" className={styles.heroButton}>
          ÏõêÎëê Îì±Î°ùÌïòÍ∏∞
        </a>
      </section>

      <section className={styles.searchSection}>
        <div className={styles.searchContainer}>
          <input
            type="text"
            placeholder="ÏõêÎëê Ïù¥Î¶ÑÏù¥ÎÇò ÏõêÏÇ∞ÏßÄÎ°ú Í≤ÄÏÉâ..."
            className={styles.searchInput}
            value={searchTerm}
            onChange={(e) => setSearchTerm(e.target.value)}
          />
          <button className={styles.filterToggle} onClick={() => setShowFilters(!showFilters)}>
            üîç ÌïÑÌÑ∞ {showFilters ? "Ïà®Í∏∞Í∏∞" : "Î≥¥Í∏∞"}
          </button>
        </div>

        {showFilters && (
          <div className={styles.filterContainer}>
            <select
              className={styles.filterSelect}
              value={filterOrigin}
              onChange={(e) => setFilterOrigin(e.target.value)}
            >
              <option value="">Î™®Îì† ÏõêÏÇ∞ÏßÄ</option>
              {origins.map((origin) => (
                <option key={origin} value={origin}>
                  {origin}
                </option>
              ))}
            </select>

            <select
              className={styles.filterSelect}
              value={filterRoast}
              onChange={(e) => setFilterRoast(e.target.value)}
            >
              <option value="">Î™®Îì† Î°úÏä§ÌåÖ</option>
              <option value="light">ÎùºÏù¥Ìä∏ Î°úÏä§Ìä∏</option>
              <option value="medium">ÎØ∏ÎîîÏóÑ Î°úÏä§Ìä∏</option>
              <option value="dark">Îã§ÌÅ¨ Î°úÏä§Ìä∏</option>
            </select>

            <select className={styles.filterSelect} value={sortBy} onChange={(e) => setSortBy(e.target.value)}>
              <option value="latest">ÏµúÏã†Ïàú</option>
              <option value="price-low">Í∞ÄÍ≤© ÎÇÆÏùÄÏàú</option>
              <option value="price-high">Í∞ÄÍ≤© ÎÜíÏùÄÏàú</option>
              <option value="popular">Ïù∏Í∏∞Ïàú</option>
            </select>

            <button
              className={styles.resetButton}
              onClick={() => {
                setSearchTerm("")
                setFilterOrigin("")
                setFilterRoast("")
                setSortBy("latest")
              }}
            >
              Ï¥àÍ∏∞Ìôî
            </button>
          </div>
        )}
      </section>

      <section id="products" className={styles.productsSection}>
        <h2 className={styles.sectionTitle}>
          {searchTerm || filterOrigin || filterRoast ? "Í≤ÄÏÉâ Í≤∞Í≥º" : "ÏµúÍ∑º Îì±Î°ùÎêú ÏõêÎëê"} ({filteredBeans.length})
        </h2>
        {filteredBeans.length === 0 ? (
          <div className={styles.emptyState}>
            <p className={styles.emptyText}>
              {beans.length === 0 ? "ÏïÑÏßÅ Îì±Î°ùÎêú ÏõêÎëêÍ∞Ä ÏóÜÏäµÎãàÎã§" : "Í≤ÄÏÉâ Í≤∞Í≥ºÍ∞Ä ÏóÜÏäµÎãàÎã§"}
            </p>
            <p className={styles.emptySubtext}>
              {beans.length === 0 ? "Ï≤´ Î≤àÏß∏Î°ú ÏõêÎëêÎ•º Îì±Î°ùÌï¥Î≥¥ÏÑ∏Ïöî!" : "Îã§Î•∏ Í≤ÄÏÉâÏñ¥Î°ú ÏãúÎèÑÌï¥Î≥¥ÏÑ∏Ïöî"}
            </p>
            {beans.length === 0 && (
              <a href="/register-bean" className={styles.registerButton}>
                ÏõêÎëê Îì±Î°ùÌïòÍ∏∞
              </a>
            )}
          </div>
        ) : (
          <div className={styles.productsGrid}>
            {filteredBeans.map((bean) => (
              <div key={bean.id} className={styles.productCard}>
                <button
                  className={styles.favoriteButton}
                  onClick={(e) => {
                    e.stopPropagation()
                    toggleFavorite(bean.id)
                  }}
                >
                  {favorites.includes(bean.id) ? "‚ù§Ô∏è" : "ü§ç"}
                </button>

                <a href={`/bean/${bean.id}`} className={styles.productLink}>
                  {bean.image && (
                    <div className={styles.productImageWrapper}>
                      <img src={bean.image || "/placeholder.svg"} alt={bean.beanName} className={styles.productImage} />
                    </div>
                  )}
                  <div className={styles.productInfo}>
                    <h3 className={styles.productName}>{bean.beanName}</h3>
                    <p className={styles.productOrigin}>{bean.origin}</p>
                    <p className={styles.productDetails}>
                      {bean.roastLevel === "light"
                        ? "ÎùºÏù¥Ìä∏ Î°úÏä§Ìä∏"
                        : bean.roastLevel === "medium"
                          ? "ÎØ∏ÎîîÏóÑ Î°úÏä§Ìä∏"
                          : "Îã§ÌÅ¨ Î°úÏä§Ìä∏"}{" "}
                      ¬∑ {bean.weight}
                    </p>
                    <p className={styles.productPrice}>{Number.parseInt(bean.price).toLocaleString()}Ïõê</p>
                    {bean.location && <p className={styles.productLocation}>üìç {bean.location}</p>}
                  </div>
                </a>

                <button
                  className={styles.addToCartButton}
                  onClick={(e) => {
                    e.stopPropagation()
                    addToCart(bean)
                  }}
                >
                  Ïû•Î∞îÍµ¨Îãà Îã¥Í∏∞
                </button>
              </div>
            ))}
          </div>
        )}
      </section>

      <footer className={styles.footer}>
        <div className={styles.footerContent}>
          <p className={styles.footerText}>¬© 2025 DabeanChi. ÏõêÎëê Í±∞Îûò ÌîåÎû´Ìèº</p>
          <p className={styles.footerText}>Î¨∏Ïùò: dcpop28201004@gmail.com</p>
        </div>
      </footer>
    </div>
  )
}
